.shoujianxiang {
    width: 90%;
    margin: 0 auto;
    .fill {
        margin-bottom: 0.9rem;
        .chatlist {
            width: 100%;
            li {
                width: 100%;
                padding: 0.3rem 0;
                box-sizing: border-box;
                border-bottom: 1px solid #eee;
                display: flex;
                justify-content: space-between;
                align-items: center;
                img {
                    width: 1rem;
                    height: 1rem;
                    border-radius: 50%;
                    border: 1px solid #eee;
                }
                .chattxt {
                    width: 80%;
                    .chattime {
                        width: 100%;
                        display: flex;
                        justify-content: space-between;
                        h2 {
                            font-size: 0.34rem;
                            line-height: 0.5rem;
                            color: #000;
                        }
                    }
                    p {
                        font-size: 0.26rem;
                        line-height: 0.5rem;
                        color: #999;
                        .chattime & {
                            font-size: 0.3rem;
                        }
                    }
                    h5 {
                        font-size: 0.28rem;
                        color: #999;
                        span {
                            color: #91D399;
                        }
                    }
                }
            }
        }
    }
}